import React from 'react';
import { Link } from 'react-router-dom';
import {
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Divider,
} from '@material-ui/core';
import {
  Home as HomeIcon,
  AddBox as AddBoxIcon,
  Settings as SettingsIcon,
  Forum as ForumIcon,
  LocationCity as LocationCityIcom,
  People as PeopleIcon,
  Map as MapIcon,
} from '@material-ui/icons';
import useStyles from './styles';

export function UserMenu () {
  const classes = useStyles();
  return (
    <List>
      <Link to="/map" className={classes.linkMenu}>
        <ListItem button>
          <ListItemIcon>
            <MapIcon />
          </ListItemIcon>
          <ListItemText></ListItemText>
        </ListItem>
      </Link>
      <Divider />
      <Link to="/cabinet" className={classes.linkMenu}>
        <ListItem button>
          <ListItemIcon>
            <HomeIcon />
          </ListItemIcon>
          <ListItemText> </ListItemText>
        </ListItem>
      </Link>
      <Link to="/cabinet/gossip" className={classes.linkMenu}>
        <ListItem button>
          <ListItemIcon>
            <ForumIcon />
          </ListItemIcon>
          <ListItemText></ListItemText>
        </ListItem>
      </Link>
      <Divider />
      <Link to="/cabinet/settings" className={classes.linkMenu}>
        <ListItem button>
          <ListItemIcon>
            <SettingsIcon />
          </ListItemIcon>
          <ListItemText></ListItemText>
        </ListItem>
      </Link>
    </List>
  );
};

